.main {
  display:flex;
  justify-content:center;
}

.box {
  max-width:500px; 
  display:flex;
  flex-direction:column;
  align-items:center;
  margin:10px;
  box-sizing: border-box;

  img {
    width:80%
  }

}

button {
  background:#ffe3b3;
  padding:10px 20px;
  border-radius:8px;
  margin:4px;
  border:1px solid #dbb97d;
  cursor: pointer;

  &:hover {
    background: #dbb97d;
    color:#fff
  }
}

.tie { border:5px solid gray; color:gray }
.lose { border:5px solid black; color:black;
  // background: fade-out(rgba(#000000, 0.5), 0.4)
  filter:opacity(30%)
}
.win { border:10px solid red; color:red }

.resultP { 
  font-size:50px;
  font-weight:700;
  text-transform: uppercase;
}
